r\n    height: 85vh;\r\n    scroll-snap-align: start;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: end;\r\n    padding: 0 0 5% 36%;\r\n}\r\n\r\n.home > div:first-child p {\r\n    color: var(--gray1);\r\n    font-family: 'Open Sans', sans-serif;\r\n    font-size: 1.2rem;\r\n    font-weight: 300;\r\n    line-height: 1.5rem;\r\n    margin-bottom: 2rem;\r\n    letter-spacing: 1.1px;\r\n}\r\n\r\n.home > div:nth-child(2){\r\n    grid-column: 1 / 2;\r\n    grid-row: 2 / 3;\r\n    height: 85vh;\r\n    scroll-snap-align: start;\r\n\r\n    font-size: 2rem;\r\n    font-family: 'Kaushan Script', cursive;\r\n    color: var(--gray2);\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: end;\r\n    padding: 0 10% 15% 36%;\r\n}\r\n\r\n.home > div:nth-child(2) div {\r\n    font-size: 2.5rem;\r\n    font-family: 'Open Sans', sans-serif;\r\n    color: var(--gray1);\r\n\r\n    padding-left: 10%;\r\n}\r\n\r\n.home > img {\r\n    grid-column: 2 / 3;\r\n    grid-row: 1 / 3;\r\n    width: 70%;\r\n    height: auto;\r\n\r\n    align-self: end;\r\n}\r\n\r\n.home > span:last-child {\r\n    position: fixed;\r\n    right: 50%;\r\n    top: 30%;\r\n    font-family: 'Open Sans', sans-serif;\r\n    font-size: 3rem;\r\n    text-align: end;\r\n}\r\n\r\n.home > span:last-child > div{\r\n    font-family: 'Kaushan Script', cursive;\r\n    font-size: 5.5rem;\r\n    text-transform: capitalize;\r\n    line-height: 1;\r\n}\r\n\r\n@keyframes fade {\r\n    0%, 40% {\r\n        opacity: 0;\r\n    }\r\n\r\n    50% {\r\n        opacity: 1;\r\n    }\r\n\r\n    70%, 100%{\r\n        opacity: 0;\r\n    }\r\n}\r\n\r\n/*---------- location ----------*/\r\n\r\n.location {\r\n    display: grid;\r\n    grid-template-columns: 0.7fr 1fr;\r\n    height: 85vh;\r\n}\r\n\r\n.location > :first-child {\r\n    grid-column: 1 / 2;\r\n    font-family: 'Open Sans', sans-serif;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: start;\r\n    align-items: center;\r\n    gap: 2rem;\r\n    padding-top: 5rem;\r\n   \r\n}\r\n\r\n.location > :first-child > label{\r\n    font-size: 3rem;\r\n    text-align: center;\r\n}\r\n\r\n.location > :first-child > label > div{\r\n    font-family: 'Kaushan Script', cursive;\r\n    font-size: 1.5rem;\r\n    color: var(--gray1)\r\n}\r\n\r\n.location > :first-child > input {\r\n    font-size: 1.5rem;\r\n    width: 70%;\r\n    border-radius: 50px;\r\n    background-color: var(--gray3);\r\n    padding: 1rem 1.5rem 1rem 1.5rem;\r\n    border: none;\r\n    color: var(--gray2);\r\n}\r\n\r\n.location > :first-child > button {\r\n    width: 30%;\r\n    height: 8%;\r\n    border-radius: 5px;\r\n\r\n    font-size: 1.5rem;\r\n    background-color: var(--gray2);\r\n    color: var(--white1);\r\n}\r\n\r\n.location > :last-child {\r\n    grid-column: 2 / 3;\r\n    align-self: center;\r\n    justify-self: center;\r\n    font-family: 'Open Sans', sans-serif;\r\n}\r\n\r\n.location > :last-child > figcaption {\r\n    text-align: center;\r\n    color: var(--red1);\r\n}\r\n\r\n.location > :last-child > iframe {\r\n    width: 800px;\r\n    border: 2px solid;\r\n}\r\n\r\n/*---------- menu ----------*/\r\n* {\r\n    /* outline: 1px solid blue;   */\r\n}\r\n\r\n.menu {\r\n    display: grid;\r\n    grid-template-columns: 0.3fr 1fr;\r\n}\r\n\r\n.menu > :first-child {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: start;\r\n    padding-top: 2rem;\r\n}\r\n\r\n.menu > div:first-child > ul {\r\n    list-style-type: none;\r\n    font-family: 'Open Sans', sans-serif;\r\n    font-size: 2rem;\r\n    border: 1px solid var(--gray1);\r\n    padding: 1rem;\r\n}\r\n\r\n.menu li {\r\n    color: var(--gray1);\r\n    transition: 0.2s;\r\n}\r\n\r\n.menu li:first-child {\r\n    margin-bottom: 1rem;\r\n}\r\n\r\n.menu li:hover{\r\n    color: var(--gray2);\r\n    font-weight: 700 ;\r\n}\r\n\r\n.menu .activeOption {\r\n    color: var(--black1);\r\n    font-weight: 700 ;\r\n}\r\n\r\n.menu > div:nth-child(2) { \r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, 30%);\r\n    row-gap: 5rem;\r\n}\r\n\r\n.menu .food-card {\r\n    grid-column: span 1;\r\n   transition: 0.3s;\r\n    width: 200px;\r\n    aspect-ratio: 1 /1;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.menu .food-image {\r\n    width: 150px;\r\n}\r\n\r\n.menu .food-card:hover  {\r\n    transform: scale(1.2);\r\n}\r\n\r\n.menu .food-card:hover .food-title {\r\n    color: var(--red1);\r\n}\r\n\r\n.menu .food-title {\r\n    font-family: 'Kaushan Script', cursive;\r\n    font-size: 1.5rem;\r\n    text-align: center; \r\n    transition: 0.2s;\r\n}\r\n\r\n.menu .food-title > span {\r\n    display: block;\r\n}\r\n\r\n/* -------- Modal ---------*/\r\n*{\r\n  /*   outline: 1px solid blue; */\r\n}\r\ndialog[open]{\r\n    width:60vw;\r\n    height: 35vw;\r\n\r\n    position: fixed; \r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n    \r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 1rem;\r\n    gap: 1rem;\r\n    border: none;\r\n}\r\n\r\ndialog > div:nth-child(2) {\r\n    flex-basis: 50%;\r\n    height: 100%;\r\n    \r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    font-family: 'Open Sans', sans-serif;\r\n    padding: 0 1rem;\r\n}\r\n\r\ndialog > div:nth-child(2) h1{\r\n   font-family: 'Kaushan Script', cursive;\r\n   font-size: 3vw;\r\n   margin-bottom: 1rem;\r\n}\r\n\r\ndialog > div:nth-child(2) p:nth-child(2){\r\n    font-size: 1.5vw;\r\n }\r\n\r\n dialog > div:nth-child(2) hr {\r\n    margin: 1rem 0;\r\n    width: 80%;\r\n    align-self: center;\r\n}\r\n\r\n dialog > div:nth-child(2) h2{\r\n    font-size: 1.5vw;\r\n }\r\n\r\ndialog > div:nth-child(2) > p:nth-child(5){\r\n    font-size: 1vw;\r\n    color: var(--gray1);\r\n }\r\n\r\n /* ----- slider ------- */\r\n * {\r\n   /*  outline: 1px solid blue; */\r\n }\r\n\r\n .image-cont {\r\n    width: 50%;\r\n    position: relative;\r\n    \r\n    /* max-width: 48rem;\r\n    margin: 0 auto;  */\r\n }\r\n\r\n .image-cont > div:first-child {\r\n    width: 100%;\r\n\r\n    overflow-x: hidden;\r\n    scroll-snap-type: x mandatory;\r\n    scroll-behavior: smooth;\r\n\r\n    display: flex;\r\n }\r\n\r\n .image-cont > div:first-child img{\r\n    width: 100%;\r\n    aspect-ratio: 1 / 1;\r\n    scroll-snap-align: start;\r\n    object-fit: contain;\r\n }\r\n\r\n.image-cont > div:nth-child(2){\r\n    position: absolute;\r\n    z-index: 1;\r\n    left: 50%;\r\n    top: 93%;\r\n    transform: translateX(-50%);\r\n    \r\n    display: flex;\r\n    column-gap: 1.5rem;\r\n }\r\n\r\n.image-cont > div:nth-child(2) a {\r\n    width: 13px;\r\n    height: 13px;\r\n    border-radius: 50%;\r\n    background-color: var(--gray1);\r\n    opacity: 0.50;\r\n    transition: 0.2s;\r\n }\r\n\r\n.image-cont > div:nth-child(2) a:hover{\r\n    opacity: 1;\r\n}\r\n\r\n.image-cont .arrow {\r\n    font-family:'Open Sans', sans-serif; ;\r\n    font-size: 3rem;\r\n    font-weight:bolder;\r\n    color: var(--gray1);\r\n    position: absolute;\r\n    top: 50%;\r\n    z-index: 1;\r\n    text-decoration: none;\r\n    transform: translateY(-50%);\r\n}\r\n\r\n.image-cont .arrow:hover {\r\n    color: var(--red1);\r\n}\r\n\r\n.image-cont > a:nth-child(3){\r\n    right: 90%;\r\n}\r\n\r\n.image-cont > a:nth-child(4){\r\n    left: 90%;\r\n}\r\n"],"sourceRoot":""}]);
